import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import moment from 'moment'
import { authx, rdbx, dbx } from "../firebase";
//mini Components
import UserList from "./min-component/userList";
import { emojis } from "./min-component/emoji";
import { AppString } from "../const";




class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            uid: null,
            name: null,
            usersList: [],
            messages: [],
            text: '',
            emoji: emojis,
            currentChatUser: null,
            stallManagerAvatar: AppString.DEFAULT_AVATAR,
            userAvatar: AppString.DEFAULT_AVATAR,
            userName: 'userName',
            userEmail: 'email',
            userCompany: '',
            isUserIsOnline: false,
            stallName: 'Stall A'
        }
    }

    //React Hook
    componentWillMount() {
        try {
            window.c = this;
            const cachedHits = localStorage.getItem('loginKey');
            if (cachedHits) {
                let cc = JSON.parse(cachedHits);
                console.log(cc)
                let uid = cc.uid;
                let username = cc.username;
                let stallNameID = cc.stallName;
                this.state.uid = uid;
                this.state.stallName = cc.stallName;
                this.setState({ uid: uid, name: username, stallName: stallNameID, externalName: cc.externalName })
                //Take Stall Manager Photo
                this.getUserAvatarImageByID(this.state.uid, 0);
                localStorage.removeItem('loginKey', []);
            } else {

                this.props.isLogout()

            }

            //End LoginInfo
            let usersList = this.state.usersList;
            var thisState = this;
            const stallDataChange = rdbx.ref().child(thisState.state.stallName);
            stallDataChange.on('child_added', snap => {
                console.log(snap.key.split('+')[1])
                let uidArr = snap.key.split('+');
                rdbx.ref().child(AppString.USERS_DOC_REALTIME_DB).child(uidArr[1]).once('value').then(function (snapshot) {

                    if (snapshot.exists()) {
                        console.log('Yes HE Chated with me');

                        if (snapshot.key != thisState.state.uid) {
                            usersList.push({
                                uid: snapshot.key,
                                name: snapshot.val().userName,
                                img: snapshot.val().profile_picture,
                                company: snapshot.val().company,
                            })
                            thisState.setState({ usersList: usersList })
                        }

                    } else {
                        console.log('No HE Chated with me');
                    }
                });
            });
        } catch (error) {
            console.log(error);
        }
    }

    componentDidUpdate() {
        this.scrollToBottom();
    }

    getToolTipData = (user) => {
        console.log("Hello");
        console.log(user.name);
    }


    //Select Single Person
    singleChat = (person) => {

        if (person !== this.state.currentChatUser) {
            //console.log(person);
            // console.log(this.state.currentChatUser);
            return;
        }
        this.setState({ messages: [] });

        let givenDB = this.state.uid + '+' + person;
        let fromDB = person + '+' + this.state.uid;
        let msgs = [];
        this.setState({ dbName: givenDB });
        const rootRef = rdbx.ref().child(this.state.stallName).child(givenDB);
        rootRef.on('child_added', snap => {
            console.log('A');
            console.log(snap.val())
            if (person !== this.state.currentChatUser) {
                //  console.log(person);
                // console.log(this.state.currentChatUser);
                return;
            }
            if (snap.val()) {
                msgs.push({
                    uid: snap.val().uid,
                    time: snap.val().time,
                    name: snap.val().stallName ? snap.val().stallName : snap.val().name,
                    msg: snap.val().text
                })
                this.setState({ messages: msgs })
            }
        });


        //Logic need to applay when admin user click then only exicute this script


        const rootRef2 = rdbx.ref().child(this.state.stallName).child(fromDB);
        rootRef2.on('child_added', snap => {

            console.log('B');
            if (person !== this.state.currentChatUser) {
                console.log(person);
                console.log(this.state.currentChatUser);
                return;
            }

            if (snap.val()) {
                msgs.push({
                    uid: snap.val().uid,
                    time: snap.val().time,
                    name: snap.val().name,
                    msg: snap.val().text
                })
                this.setState({ messages: msgs })
            }
        });
        //console.log(givenDB);
        //console.log(fromDB);
    }

    sortArrayElements = (msgs) => {
        for (let i = 0; i < msgs.length; i++) {
            for (let j = i + 1; j < msgs.length; j++) {
                if (msgs[i].time > msgs[j].time) {
                    let tmp = msgs[i];
                    msgs[i] = msgs[j];
                    msgs[j] = tmp;
                }
            }
        }
        return msgs;
    }

    //Send Message
    pushMsg = (event) => {
        event.preventDefault();
        let uid = this.state.uid;
        let name = this.state.stallName;//this.state.name; //for specific reson
        let text = this.state.text;
        let dbName = this.state.dbName;

        let uidArr2 = dbName.split('+');
        if (uid === uidArr2[1]) {
            console.log('Can not message to Me and Me');
            return;
        }

        if (text.trim() === '') {
            return
        } else {
            //notification
            if (dbName !== 'chatRoom') {
                let uidArr = dbName.split('+');
                const removeNotification = rdbx.ref('notification').child(uid).child(uidArr[1]);
                const notification = rdbx.ref().child('notification').child(uidArr[1]);

                notification.child(uid).once('value', snapshot => {
                    if (snapshot.val()) {
                        notification.child(uid).set({
                            count: snapshot.val().count + 1
                        })
                    } else {
                        notification.child(uid).set({
                            count: 1
                        })
                    }
                })
                removeNotification.set({
                    count: 0
                })
            }

            const rootRef = rdbx.ref().child(this.state.stallName).child(dbName);
            rootRef.push().set({
                uid: uid,
                time: Date.now(),
                name: name,
                stallName: this.state.externalName,
                text: text
            });
            this.setState({ text: '' });
        }
    }

    getUserAvatarImageByID = (id, userType) => {

        const userRef = rdbx.ref(AppString.USERS_DOC_REALTIME_DB);
        let defaultUrl = AppString.DEFAULT_AVATAR;
        var snapThis = this;

        userRef.on('value', (snapshot) => {
            snapshot.forEach(function (child) {
                if (id === child.key) {
                    defaultUrl = child.val().profile_picture;
                    if (userType === 0) {
                        snapThis.setState({ stallManagerAvatar: defaultUrl });
                    } else {
                        snapThis.setState({ userAvatar: defaultUrl, userName: child.val().fullName, userEmail: child.val().email, userCompany: child.val().company });
                    }
                }
            })

        })

        if (userType === 0) {
            snapThis.setState({ stallManagerAvatar: defaultUrl });
        } else {
            this.setState({ userAvatar: defaultUrl });
        }

    }

    //Scroll message UI-kit
    scrollToBottom = () => {
        const messagesContainer = ReactDOM.findDOMNode(this.messagesContainer);
        //console.log(messagesContainer.scrollHeight);
        if (messagesContainer)
            messagesContainer.scroll(0, messagesContainer.scrollHeight);
    }
    // emoji open
    openEmoji = () => {
        let emoji = document.querySelector('.emoji');
        (emoji.style.display === 'block')
            ? emoji.style.display = "none"
            : emoji.style.display = "block"
    }
    openEmojiClose = () => {
        let emoji = document.querySelector('.emoji');
        if (emoji.style.display === 'block') { emoji.style.display = "none" }
    }

    pickEmoji = (emo) => {
        let prevText = this.state.text;
        this.setState({ text: prevText + emo })
    }

    signout = () => {
        localStorage.setItem('loginKey', []);

        this.props.isLogout()
    }
    //Sorting by time
    srotingMessageFromTime = (a, b) => {
        return a.time > b.time;
    }

    getUserData = (id) => {

        // var docRef = dbx.collection("users").doc(id);
        let self = this;
        // docRef.get().then(function (doc) {
        //     if (doc.exists) {
        //         console.log("Document data:", doc.data());
        //         if (doc.data().company) {
        //             self.setState({ userCompany: doc.data().company })
        //         } else {
        //             self.setState({ userCompany: 'Company Name Not Found' })
        //         }

        //     } else {
        //         // doc.data() will be undefined in this case
        //         console.log("No such document!");
        //         self.setState({ userCompany: 'Company Name Not Found' })
        //     }
        // }).catch(function (error) {
        //     console.log("Error getting document:", error);
        //     self.setState({ userCompany: 'Company Name Not Found' })
        // });


        //Online Off Line Check
        const userOnlineCheck = rdbx.ref().child(AppString.STALL_ONLINE).child(id);
        userOnlineCheck.on('value', snap => {
            if (snap.exists()) {
                if ("offline" === snap.val().State.StateMode) {
                    self.setState({ isUserIsOnline: false })
                } else {
                    self.setState({ isUserIsOnline: true })
                }
            } else {
                self.setState({ isUserIsOnline: false })
            }
        });

    }

    selectChatUser = (id) => {
        this.state.currentChatUser = id;
        this.setState({ currentChatUser: id })
        this.singleChat(id);
        this.getUserAvatarImageByID(id, 1);
        this.getUserData(id);
    }

    render() {
        return (
            <div>

                <div className="main__container">
                    <div className='top-header'>

                        <img src={this.state.stallManagerAvatar} alt="avatar" width="36" />
                        <div className="stallName">{this.state.stallName}</div>
                        <button className="signout" onClick={this.signout}> Signout </button>
                    </div>

                    <div className="wrapper">

                        <div className="left__section scrollbar-style">
                            <UserList
                                usersList={this.state.usersList}
                                uid={this.state.uid}
                                singleChat={this.selectChatUser.bind(this)}
                                logout={this.props.isLogout}
                            />
                        </div>

                        <div className="right__section">
                            {
                                this.state.currentChatUser === null ? null : (
                                    <div className="user-data-info">
                                        <div> <img src={this.state.userAvatar} alt="avatar" className="user-data-info-image" /> </div>
                                        <span className="user-data-info-text">
                                            <strong style={{ textTransform: 'capitalize', color: '#000' }}>{this.state.userName}</strong>
                                        </span>
                                        <span className="user-data-info-text">
                                            <strong style={{ textTransform: 'capitalize', color: '#000' }}>{this.state.userEmail}</strong>
                                        </span>
                                        <span className="user-data-info-text">
                                            <strong style={{ textTransform: 'capitalize', color: '#000' }}>{this.state.userCompany}</strong>
                                        </span>
                                        <span className="user-data-info-text">
                                            <strong className={this.state.isUserIsOnline ? 'onlineUser' : 'offlineUser'}>{this.state.isUserIsOnline ? 'Online' : 'Offline'}</strong>
                                        </span>
                                    </div>)
                            }

                            {/* checking current chat User is null  */}
                            {this.state.currentChatUser === null ?
                                (<div style={{
                                    textAlign: 'center',
                                    fontSize: '2rem',
                                    fontWeight: 'bold',
                                }}>
                                    Plese select any user
                                </div>) : (



                                    <div className="message__box scrollbar-style" ref={(el) => { this.messagesContainer = el; }}>

                                        {this.sortArrayElements(this.state.messages).map(
                                            (message, i) => {
                                                if (message.uid === this.state.uid) {
                                                    return (
                                                        <div className="msg__text" key={i}>

                                                            <div> <img src={this.state.stallManagerAvatar} alt="avatar" /> </div>

                                                            <span>
                                                                <strong style={{ textTransform: 'capitalize', color: '#9E9E9E' }}>{this.state.stallName}</strong><br />
                                                                {message.msg}
                                                                <br />
                                                                <small style={{ color: '#9E9E9E' }}>{moment(message.time).fromNow()}</small>
                                                            </span>
                                                        </div>
                                                    )
                                                } else {
                                                    return (
                                                        <div className="msg__text__right" key={i}>
                                                            <div> <img src={this.state.userAvatar} alt="avatar" /> </div>
                                                            <span>
                                                                <strong style={{ textTransform: 'capitalize', color: '#9E9E9E' }}>{message.name}</strong><br />
                                                                {message.msg}
                                                                <br />
                                                                <small style={{ color: '#9E9E9E' }}>{moment(message.time).fromNow()}</small>
                                                            </span>
                                                        </div>
                                                    )
                                                }
                                            }
                                        )
                                        }

                                    </div>

                                )}

                            {this.state.currentChatUser !== null ? (

                                <form>
                                    <div className="message__type__box">
                                        <input
                                            placeholder="Type Your Message"
                                            onChange={(e) => { this.setState({ text: e.target.value }) }}
                                            value={this.state.text}
                                            require="true"
                                            onFocus={this.openEmojiClose}
                                        />
                                        <span className="emojiIcon" onClick={this.openEmoji}> â˜º </span>
                                        <button type="submit" onClick={this.pushMsg} > <i className="glyphicon glyphicon-send"></i> </button>
                                    </div>
                                </form>

                            ) : null}



                        </div>

                    </div>


                    <div className="emoji">
                        {this.state.emoji.map(
                            (emo, i) =>
                                <a key={i} onClick={() => { this.pickEmoji(emo) }}> <span role="img">{emo}</span> </a>
                        )}
                    </div>

                </div>

            </div >
        )
    }
}

export default Home;
