import React, { Component } from "react";
import { authx,rdbx,dbx } from "../firebase";
import { AppString } from "../const";
 
class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      display: 'inline-block', 
      email: '',
      password: '',
      error: null,
      alreaylogged: false
    };
  }

  loginWithGmail = e => {
    e.preventDefault();

   { console.log('Clicked On Login')}
    
     const { email,password } = this.state;

     const pass = `${email}123456`
    
  
    authx.signInWithEmailAndPassword(email,pass).then(result => {
        //If already registred user
        let userRef = rdbx.ref().child(AppString.USERS_DOC_REALTIME_DB).child(result.user.uid);
        
        userRef.once("value", snapshot => {
          var isAvailable = snapshot.val();
          if (!isAvailable) {
            this.addUserList(result);
          } else {
            //Check Default Image Is Their or not
            if(result.user.photoURL){
              userRef.update({ profile_picture: result.user.photoURL });
            }else{
              userRef.update({ profile_picture: AppString.DEFAULT_AVATAR });
            }
          }
        });

      let isJoinedStall=false;
      dbx.collection(AppString.ADMIN_DOC).doc(AppString.ADMIN_TABLE).get().then(fun=>{ 
          let stallAdminList=fun.data();

          for(let i=0;i<stallAdminList.AdminList.length;i++){
            if(stallAdminList.AdminList[i].adminUID===result.user.uid)
            {
              isJoinedStall=true;
              localStorage.setItem(
                "loginKey",
                JSON.stringify({
                  uid: result.user.uid,
                  username: result.user.displayName || "Admin",
                  stallName:stallAdminList.AdminList[i].stallName,
                  externalName: stallAdminList.AdminList[i].externalName
                })
              );
              //Redirecting to home page
              this.props.isLogin();
              console.log("Success Login");
              break;
            }

          }

          if(!isJoinedStall){
            this.setState({ error: 'You have no permission to login in Stall Admin Area' });
          }
      });
      
      })
      .catch(error => {
        var errorMessage = error.message;
        this.setState({ error: errorMessage });
      });
  };

  handleChange(e, name) {
    this.handleStateChange([name], e.target.value);
  }

  handleStateChange(name, value) {
    this.setState({ [name]: value });
  }


  addUserList = result => {
    const rootRef = rdbx.ref().child(AppString.USERS_DOC_REALTIME_DB).child(result.user.uid);

    let userImage='';
    if(result.user.photoURL){
      userImage= result.user.photoURL;
    }else{
      userImage= AppString.DEFAULT_AVATAR;
    }


    rootRef.set({
      userName:result.user.displayName,
      profile_picture: userImage,
      fullName: result.user.displayName,
      email: result.user.email
    });

  };

  render() {
    return (
      <div>
        <div className="container">

          <div className="row">
            
            <div className="col-md-6 col-md-offset-3 outer-box-login">

              <img src="logo.png"></img>
              <h3>Admin Chat Login</h3>
              <br />
              
              <div className="login-area">

              <div className="form-group">
                  <input
                    id="email"
                    className="form-control"
                    type="text"
                    placeholder="Enter your email ID"
                    onChange={(e) => this.handleChange(e, "email")}
                    value={this.state.email}
                    required
                  />
                </div>

                {/* <div className="form-group">
                  <input
                    className="form-control"
                    type="text"
                    placeholder="Enter your password"
                    id="password"
                    onChange={(e) => this.handleChange(e, "password")}
                    value={this.state.password}
                    required
                  />
                </div> */}
             
              <button
                type="button"
                className="login-btn"
                onClick={this.loginWithGmail}
                style={{ display: this.state.display }}
              >
                Login
              </button>
              <br />
              <br />

              {this.state.error ? (
                <div className="alert alert-danger" role="alert">
                  {this.state.error}
                </div>
              ) : (
                ""
              )}

              </div>
                

            </div>

          </div>
          
        </div>
      </div>
    );
  }
}

 
export default Login;
